// 两位小数的限制
export const TWO_DECIMAL_REGEXP = /^\d*(\.\d{0,2})?$/;

// 验证零和非零开头的数字：
export const nomalDigit = /^(0|[1-9][0-9]*)$/;

// 数字类型
export const numberFixedTen = /^(([1-9][0-9]*)+|0)(\.[0-9]{1,10})?$/;
// 网址正则
export const URL_REGEXP = /https?:\/\/[-A-Za-z0-9+&@#/%?=~_|!:,.;]+[-A-Za-z0-9+&@#/%=~_|]/;

// 支持18位和15位社会信用代码, 一社会信用代码由18位数字或者大写字母组成，但是字母不包括 I、O、Z、S、V
// 一共由五部分组成
// 第一部分：登记管理部门代码1位 (数字或大写英文字母)
// 第二部分：机构类别代码1位 (数字或大写英文字母)
// 第三部分：登记管理机关行政区划码6位 (数字)
// 第四部分：主体标识码（组织机构代码）9位 (数字或大写英文字母)
// 第五部分：校验码1位 (数字或大写英文字母)
export const SOCIAL_UNIFIED_CREDIT_CODE_REGEXP = /[0-9A-HJ-NPQRTUWXY]{2}\d{6}[0-9A-HJ-NPQRTUWXY]{10}/;


export function checkURL(url: string) {
  var str = url;
  //判断URL地址的正则表达式为:http(s)?://([\w-]+\.)+[\w-]+(/[\w- ./?%&=]*)?
  //下面的代码中应用了转义字符"\"输出一个字符"/"
  var Expression = /http(s)?:\/\/([\w-]+\.)+[\w-]+(\/[\w- .\/?%&=]*)?/;
  var objExp = new RegExp(Expression);
  if (objExp.test(str) == true) {
    return true;
  } else {
    return false;
  }
}
