export const isSSOLoggedOut = async (): Promise<boolean> => {
  const instance = new ACenterSdk(SDKConfig);
  try {
    return false;
  } catch (err) {
    return true;
  }
  return true;
};

// 判断SSO站点是否有account登陆态，如果有则回种子站点
export const isAffSSOLoggedOut = async (): Promise<boolean> => {
  const instance = new ACenterSdk(SDKConfig);
  // service为登陆成功之后的回调地址
  try {
    return false;
  } catch (err) {
    return true;
  }
  return true;
};
const isSubDomainLoggedOut = async () => {
  return true;
};

// 判断子站点是否有account登陆态
const isAffSubDomainLoggedOut = async (userInfo?: IUserInfo) => {
  if (userInfo?.shop_type === EShopType.INSTITUTIONAL_SERVICE_PROVIDERS) {
    const origin_isSubDomainLogout = userInfo.user_id === '' || userInfo.user_id === '0';
    return origin_isSubDomainLogout;
  }
  if (userInfo?.shop_type === EShopType.DAREN) {
    const origin_isSubDomainLogout =
      userInfo.user_id === '' || userInfo.user_id === '0' || userInfo.origin_uid === '' || userInfo.origin_uid === '0';
    return true;
  }
  // 商家角色
  return (
    userInfo?.user_id === '' || userInfo?.user_id === '0' || userInfo?.origin_uid === '' || userInfo?.origin_uid === '0'
  );
};

export const isUserLogout = async () => {
  const hasSubDomainLoggedOut = await isSubDomainLoggedOut();
  if (!hasSubDomainLoggedOut) {
    return false;
  }
  const hasSSODomainLoggedOut = await isSSOLoggedOut();
  if (!hasSSODomainLoggedOut) {
    return false;
  }
  return true;
};

// account登陆态判断
export const isUserAffLogout = async (userInfo?: IUserInfo): Promise<boolean> => {
  // account 子站点登陆态
  const hasAffSubDomainLoggedOut = await isAffSubDomainLoggedOut(userInfo);
  if (!hasAffSubDomainLoggedOut) {
    return false;
  }
  // account SSO登陆态
  const hasAffSSODomainLoggedOut = await isAffSSOLoggedOut();
  if (!hasAffSSODomainLoggedOut) {
    return false;
  }

  return true;
};



// 是否有登陆态
export const isLogout = (userInfo: IUserInfo) => {

  if (
    [
      EShopType.INSTITUTIONAL_SERVICE_PROVIDERS,
      EShopType.INSTITUTIONAL_TOOLS_SERVICE_PROVIDERS,
      EShopType.DOUKE,
    ].includes(userInfo?.shop_type)
  ) {
    return userInfo?.user_id === '' || userInfo?.user_id === '0';
  }
  return (
    userInfo?.user_id === '' || userInfo?.user_id === '0' || userInfo?.origin_uid === '' || userInfo?.origin_uid === '0'
  );
};

// 当前选择的 shopType 是否已经开通
export const isShopTypeSettled = (userInfo: IUserInfo, shopType?: string) => {
  const shop = userInfo.shops.find(shopItem => shopItem.shop_type.toString() === shopType);
  return Boolean(shop);
};

// 当前选择的 shopType 是否已经同意协议
// 此 check 其实包含了上面的 isShopTypeSettled
// 因为需要先开通 shop，才能有协议签署状态
export const isShopTypeProtocolAgreed = (userInfo: IUserInfo, shopType: string | EShopType) => {
  if (Number(shopType) === EShopType.INSTITUTIONAL_SERVICE_PROVIDERS) {
    return userInfo.agree_pro === EAgreePro.AGREE_INSTITUTION;
  }
  return userInfo.shops.some(
    shop =>
      shop.shop_type.toString() === shopType &&
      (shop.agree_protocol === EAgreePro.AGREE || shop.agree_protocol === EAgreePro.AGREE_INSTITUTION)
  );
};

/**
 * 新增方法：旧机构入驻流程（未拆分角色前）使用
 * shopType为机构 23 时，是否签署协议
 * agree_pro 为 1 时未签署
 * agree_pro 为 2 时为签署完第一个协议
 * agree_pro 为 3 时为签署完第二个协议
 */
export const isSignCommonProtocol = (userInfo: IUserInfo, shopType: string | EShopType) => {
  if (Number(shopType) === EShopType.INSTITUTIONAL_SERVICE_PROVIDERS) {
    return userInfo.agree_pro === EAgreePro.AGREE || userInfo.agree_pro === EAgreePro.AGREE_INSTITUTION;
  }
  return;
};

// 是否需要邀请码
export const needInviteCode = (userInfo: IUserInfo) =>
  (userInfo.shop_id === '' || userInfo.shop_id === '0') && !userInfo.shops;

// 是否同意过协议 true代表同意过
export const isAgreeProtocol = (userInfo: IUserInfo) =>
  userInfo.agree_pro === EAgreePro.AGREE || userInfo.agree_pro === EAgreePro.AGREE_INSTITUTION;

// 是否同意过机构协议 true代表同意过
export const isAgreeInstitutionProtocol = (userInfo: IUserInfo) => userInfo.agree_pro === EAgreePro.AGREE_INSTITUTION;


// 有多个角色但未选择角色
export const hasMutltiRoles = (userInfo: IUserInfo) => {
  if (userInfo.shops && userInfo.shops.length > 0 && userInfo.shop_id === '0') {
    return true;
  }
  return false;
};

// 所有开通角色被关闭但是还有角色可开启
export const shouldRedirectToRolesManage = (userInfo: IUserInfo) => {
  if (!hasMutltiRoles(userInfo)) {
    return false;
  }
  const settledRolesLength = userInfo.shops.length;
  const closeRoles = userInfo.shops.filter((shop: IShopType) => shop.status !== EStatus.OPEN);
  if (closeRoles.length === settledRolesLength && settledRolesLength < 5) {
    return true;
  }
  return false;
};

// 需要选择角色
export const needSelectRole = (userInfo: IUserInfo) => {
  // 没有已开通角色
  if (!userInfo.shops || userInfo.shops.length < 1) {
    return true;
  }
  // 已经开通角色但未选择
  if (hasMutltiRoles(userInfo) && !shouldRedirectToRolesManage(userInfo)) {
    return true;
  }
  return false;
};

// 是否需要同意抖拍档协议
export const doupaidangShopType = (userInfo: IUserInfo) => {
  const shopType = userInfo.shop_type;
  const shopTypeChild = userInfo.shop_type_child;
  const accountChildType = userInfo.account_child_type as any;
  // 机构并且子角色为1、2、3不走抖排挡判断
  if (shopType === EShopType.INSTITUTIONAL_SERVICE_PROVIDERS && [1, 2, 3].includes(accountChildType)) {
    return false;
  }
  return (
    shopType === EShopType.INSTITUTIONAL_SERVICE_PROVIDERS &&
    (shopTypeChild === EInsitutionalServiceProvidersChild.DOUPAIDANG_BRAND_PROVIDERS ||
      shopTypeChild === EInsitutionalServiceProvidersChild.DOUPAIDANG_TRAIN_PROVIDERS ||
      shopTypeChild === EInsitutionalServiceProvidersChild.DOUPAIDANG_CONTENT_PROVIDERS)
  );
};

// 是否需要同意抖拍档协议
export const needSignDoupaidangProtocol = (userInfo: IUserInfo) => {
  if (needSelectRole(userInfo) || shouldRedirectToRolesManage(userInfo)) {
    return false;
  }
  if (isAgreeProtocol(userInfo)) {
    return false;
  }
  return doupaidangShopType(userInfo);
};

// 小店商家
export const smallShop = (userInfo: IUserInfo) => {
  const shopType = userInfo.shop_type;
  return shopType === EShopType.INTERNAL_BUSINESS;
};

// 达人
export const daren = (userInfo: IUserInfo) => {
  const shopType = userInfo.shop_type;
  return shopType === EShopType.DAREN;
};

// 新抖客
export const douke = (userInfo: IUserInfo) => {
  const shopType = userInfo.shop_type;
  return shopType === EShopType.DOUKE;
};

// 入驻成功抖客
export const normalDouke = (userInfo: IUserInfo) => {
  const { account_status, account_child_status, agree_pro } = userInfo;
  return (
    douke(userInfo) &&
    account_status === EStatus.OPEN &&
    account_child_status === EAccountChildStatus.NORMAL &&
    agree_pro === EAgreePro.AGREE
  );
};

// 达人任务-任务管理
export const darenTask = async (userInfo: IUserInfo) => {
  let isAB = false;
  if (process.env.PROXY_ENV === 'ppe' || process.env.BUILD_ENV === 'ppe' || process.env.BUILD_ENV === 'online') {
    isAB = await new Promise(resolve => {
      window.collectEvent('getVar', 'is_showing_task', false, (value: boolean) => {
        resolve(value);
      });
    });
  }
  const shopType = userInfo.shop_type;
  const now = new Date().getTime();
  // 新手引导开到22年1月15日
  if (now > new Date('2022-01-15').getTime()) {
    return false;
  }
  return shopType === EShopType.DAREN && isAB;
};

// 是否需要签署小店联盟协议
export const needSignShopProtocol = (userInfo: IUserInfo) => {
  if (needSelectRole(userInfo) || shouldRedirectToRolesManage(userInfo)) {
    return false;
  }
  if (isAgreeProtocol(userInfo)) {
    return false;
  }
  return smallShop(userInfo);
};

// 是否需要签署普通的协议
export const needSignCommonProtocol = (userInfo: IUserInfo) => {
  if (needSelectRole(userInfo) || shouldRedirectToRolesManage(userInfo)) {
    return false;
  }
  if (daren(userInfo)) {
    return false;
  }
  if (location.pathname === '/dashboard/author/role') {
    return false;
  }
  return !isAgreeProtocol(userInfo) && !needSignDoupaidangProtocol(userInfo) && !needSignShopProtocol(userInfo);
};

// 抖拍档商家条件
export const doupaidangUser = (userInfo: IUserInfo) =>
  !needInviteCode(userInfo) &&
  isAgreeProtocol(userInfo) &&
  doupaidangShopType(userInfo) &&
  userInfo.status === EStatus.OPEN;

// 机构
export const institution = (userInfo: IUserInfo) => {
  const shopType = userInfo.shop_type;
  return shopType === EShopType.INSTITUTIONAL_SERVICE_PROVIDERS;
};

export const closedInstitution = (userInfo: IUserInfo, isClear = false) => {
  if (!institution(userInfo)) return false;
  const { status, child_status, account_child_status } = userInfo;
  return isClear
    ? status === EStatus.OP_CLOSE
    : status === EStatus.OP_CLOSE ||
    child_status === EChildStatus.TRIAL ||
    account_child_status === EAccountChildStatus.UNDER_CONTROL;
};

// 正常商家条件
export const normalUser = (userInfo: IUserInfo) => {
  if (needSelectRole(userInfo) || shouldRedirectToRolesManage(userInfo)) {
    return true;
  }

  return (
    !needInviteCode(userInfo) &&
    (isAgreeProtocol(userInfo) || daren(userInfo) || smallShop(userInfo)) &&
    !doupaidangShopType(userInfo) &&
    (closedInstitution(userInfo) || userInfo.status === EStatus.OPEN)
  );
};

// 生鲜百应角色
export const freshBuyin = (userInfo: IUserInfo) => {
  if (location.pathname === SHOP_SETTLE_PATH) {
    return false;
  }
  const shopType = userInfo.shop_type;
  const shopTypeChild = userInfo.shop_type_child;
  return shopType === EShopType.INTERNAL_BUSINESS && shopTypeChild === EInternalBusinessChild.FRESH_BUYIN;
};

// 工具服务商
export const serviceProvider = (userInfo: IUserInfo) => {
  const shopType = userInfo.shop_type;
  return shopType === EShopType.INSTITUTIONAL_TOOLS_SERVICE_PROVIDERS;
};

// 闪购达人
export const darenFlash = (userInfo: IUserInfo) => {
  const shopType = userInfo.shop_type;
  const shopTypeChild = userInfo.shop_type_child;
  return shopType === EShopType.DAREN && shopTypeChild === EDarenChild.DAREN_FLASH;
};

// 普通机构服务商
export const normalServiceProviders = (userInfo: IUserInfo) => {
  const shopType = userInfo.shop_type;
  // 兼容下机构拆分角色后的判断 & 以前普通机构服务商的判断
  const accountChildType = userInfo.account_child_type || userInfo.shop_type_child;
  return (
    shopType === EShopType.INSTITUTIONAL_SERVICE_PROVIDERS &&
    [EChildShopType.MCN, EChildShopType.ZHAO_SHANG_TUAN_ZHANG, EChildShopType.DOU_KE].includes(accountChildType)
  );
};

// 是否需要同意平台机构入驻协议
export const commonInstitutionProvidersNeedSignProtocol = (userInfo: IUserInfo) =>
  normalServiceProviders(userInfo) && !isAgreeInstitutionProtocol(userInfo);

// 外部商家
export const popMerchants = (userInfo: IUserInfo) => {
  const shopType = userInfo.shop_type;
  return shopType === EShopType.BRAND_MERCHANTS;
};

// 电商平台
export const ecom_platform = (userInfo: IUserInfo) => {
  const shopType = userInfo.shop_type;
  return shopType === EShopType.ECOM_PLATFORM;
};

// 是否是机构入驻中，机构账户状态99也代表入驻中
export const isInstitutionPending = (userInfo: IUserInfo) => {
  // 判断是否点击过立即入驻按钮---机构新增保证金含外链跳转
  // const needEntryConfirm = JSON.parse(localStorage.getItem(INSTITUTION_CONFIRM_KEY) ?? '{}');
  const needInstitutionConfirm = false;

  return (
    normalServiceProviders(userInfo) &&
    userInfo.account_status !== ECheckStatus.BANNED &&
    (userInfo.check_status === ECheckStatus.PENDING ||
      userInfo.account_status === ECheckStatus.CHILD_ROLE_PENDING ||
      userInfo.account_child_status === EAccountChildStatus.GUARANTEE_PENDING ||
      needInstitutionConfirm)
  );
};

export const isNormalInsititutionToolsServiceProviders = (userInfo: IUserInfo) =>
  userInfo.shop_type === EShopType.INSTITUTIONAL_TOOLS_SERVICE_PROVIDERS &&
  userInfo.agree_pro === EAgreePro.AGREE &&
  userInfo.account_status === 1;

// 不需要校验用户状态渲染菜单的情形
export const notNeedCheckUser = (userInfo: IUserInfo) => isInstitutionPending(userInfo);

// 是否为小店商家入驻中状态
export const isSmallShopPending = (userInfo: IUserInfo) =>
  smallShop(userInfo) && userInfo.check_status === ECheckStatus.PENDING;
// 是否是商家类型
export const isMerchShopList = (userInfo: IUserInfo): boolean =>
  smallShop(userInfo) || ecom_platform(userInfo) || popMerchants(userInfo);

// 是否为游客身份
export const isVisitor = (userInfo: IUserInfo): boolean =>
  userInfo.check_status === ECheckStatus.PENDING && userInfo.status === EStatus.OPEN;

// 是否需要签署协议
export const isNeedShopAgreePro = (userInfo: IUserInfo): boolean => Boolean(userInfo.shop_need_agree_pro);

// 是否为有精选联盟权限的小店商家（已开通试用版/正式版 并且已经签署协议）:非游客身份
export const hasAllianceAuthSmallShop = (userInfo: IUserInfo): boolean =>
  smallShop(userInfo) && !isVisitor(userInfo) && !isNeedShopAgreePro(userInfo);

// 是否为绑定星图商家
export const isBindStar = (userInfo: IUserInfo): boolean =>
  isMerchShopList(userInfo) && userInfo.has_bind_star === EBindStar.OPEN;

// 是否是拥有营销设置权限的商家
export const hasMarketing = (userInfo: IUserInfo): boolean => userInfo.marketing_create_coupon_auth === 1;

// 根据用户类型获取用户类型中文名
export const getUserTypeName = (userInfo: IUserInfo): string => {
  if (daren(userInfo)) {
    return '达人';
  }
  if (smallShop(userInfo)) {
    return '商家';
  }
  if (normalServiceProviders(userInfo)) {
    return '机构';
  }
  return '';
};

// 根据用户类型获取用户id
export const getUserTypeID = (userInfo: IUserInfo): string => {
  if (daren(userInfo)) {
    return userInfo.user_id;
  }
  if (smallShop(userInfo)) {
    return userInfo.shop_id;
  }
  if (normalServiceProviders(userInfo)) {
    return userInfo.origin_uid;
  }
  return '';
};

export const darenSquare = (userInfo: IUserInfo): boolean => {
  const { daren_plaza_status } = userInfo;
  return daren_plaza_status !== 0 && daren(userInfo);
};

/**
 * @author lengfangbing
 * @param {IUserInfo} userInfo 返回的用户信息
 * @returns {boolean} 是否是KA用户
 */
export const isKeyAccount = (userInfo: IUserInfo): boolean =>
  // 只有是达人并且是KA用户才可以展示
  daren(userInfo) && userInfo.isKeyAccount === 1;

// 是达人且等级大于LX可见
export const isConformLevelDaren = (userInfo: IUserInfo, level: number): boolean =>
  daren(userInfo) && userInfo.author_level > level;

export const USER_TYPES = {
  daren,
  darenFlash,
  normalServiceProviders,
  popMerchants,
  ecom_platform,
  smallShop,
  freshBuyin,
  normalUser,
  needSelectRole,
  shouldRedirectToRolesManage,
  commonInstitutionProvidersNeedSignProtocol,
  isMerchShopList,
  isVisitor,
  hasAllianceAuthSmallShop,
  isBindStar,
  darenSquare,
  isKeyAccount,
  isConformLevelDaren,
};
