import { ErrorBoundary } from 'react-error-boundary';
import { BrowserRouter as Router, Route, Switch, withRouter } from 'react-router-dom';
import { BaseRoute, routes as staticRoutes } from 'src/router';
import { SubAppErrorFallback } from '@global/utils/errors';
import { NotFoundPage } from '@global/utils/errors/not-find-page';
