import sp_zh_CN from 'antd/locale/zh_CN';
export interface ILoadableCompProps {
  userInfo: IUserInfo;
  location: Location;
  isIndividual?: boolean;
}
import { defUserInfo } from "@global/domain/user/common";
import { initStorageUserInfo } from '@global/domain/user/user';
import { defaultUserInfo } from '@global/domain/user/init';

const zeroAppDom = document.getElementById("zeroApp");


const lazy = (componentPath: string, props: ILoadableCompProps) => {
  const AsyncRoute: ILocalRoute = staticRoutes.find(ele => ele.path === componentPath) as ILocalRoute;

  const AsyncPage = AsyncRoute?.component;
  const AsyncRemotePage = AsyncRoute?.remoteComponent;

  if (AsyncRemotePage) {
    const [remoteType, remoteModule] = AsyncRemotePage.split('/');
    return () => (
      <ErrorBoundary
        FallbackComponent={SubAppErrorFallback}
        onError={(
          error: Error,
          info: {
            componentStack: string;
          }
        ) => {
          console.error('uploading error info', error, info);
        }}
      >
        <FederatedComponent remoteType={remoteType} scope={remoteType} module={remoteModule} {...props} />
      </ErrorBoundary>
    );
  } else if (AsyncPage) {
    return () => (
      <ErrorBoundary
        FallbackComponent={SubAppErrorFallback}
        onError={(
          error: Error,
          info: {
            componentStack: string;
          }
        ) => {
          console.error('uploading error info', error, info);
        }}
      >
        <AsyncPage {...props} />
      </ErrorBoundary>
    );
  } else {
    // 404提示页
    return () => (
      <ErrorBoundary
        FallbackComponent={SubAppErrorFallback}
        onError={(
          error: Error,
          info: {
            componentStack: string;
          }
        ) => {
          console.error('uploading error info', error, info);
        }}
      >
        <NotFoundPage />
      </ErrorBoundary>
    );
  }
};
function App(props: ILoadableCompProps) {
  const routes: ILocalRoute[] = getSubAppRoutes(staticRoutes);

  const ScrollToTop = withRouter(_routerGuard);

  const enableDevtool = process.env.NODE_ENV === 'development' || process.env.BUILD_ENV === 'ppe';
  sessionStorage.setItem('userInfo', JSON.stringify(defaultUserInfo))
  const { userInfo } = initStorageUserInfo(defaultUserInfo);
  // console.log('guide - domainUser', domainUser, defaultUserInfo)

  import('@global/domain/guides/index').then(({ BuyinGuideSDK }) => {
    BuyinGuideSDK.init(userInfo, enableDevtool);
    // Garfish.setGlobalValue('BuyinGuideSDK', BuyinGuideSDK);
  });

  return (
    <Router basename="/">
      <ScrollToTop routes={routes}>
        <div style={{ padding: 24 }}>
          <Switch>
            {routes.map((item: { path: string }) => (
              <Route key={item.path + new Date().getTime()} exact path={item.path} component={lazy(item.path, props)} />
            ))}
          </Switch>
        </div>
      </ScrollToTop>
    </Router>
  );
}
