import { BaseRoute } from "src/router";

export interface ILocalRoute extends BaseRoute {
  remoteComponent?: string;
  permission?: boolean;
}
export function getSubAppRoutes(subAppRoutes: any): ILocalRoute[] {
  return subAppRoutes.map((item: any) => {
    // 组件的路径：如果有指定componentPath就取，没有就取path
    const componentPath = item?.component;
    return {
      path: item.path,
      componentPath,
      name: item?.name,
      permission: item.permission,
    };
  });
};
