
import { useLocation } from 'react-router-dom';
import { useEffect } from 'react';
import { Modal } from 'antd';
import logger from '../logger';

export function _routerGuard(props: any) {
  const { routes } = props;
  const { pathname } = useLocation();

  /** 路由切换时设置page_name为看门人菜单名称 */
  useEffect(() => {
    try {
      if (routes?.length) {
        const currRouterObj = routes.find(ele => {
          return pathname === `${ele.path}`;
        });

        if (currRouterObj) {
          logger.setConfig({ page_name: currRouterObj.name });
        }
      }
    } catch (e) {}
  }, [pathname, routes]);
  useEffect(() => {
    /** 切换路由时页面置顶 */
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;
  }, [pathname]);

  useEffect(() => {
    // 切换路由，销毁所有弹窗
    Modal.destroyAll();
  }, [pathname]);
  return props.children;
}
