import {
  IUserInfo,
  needSelectRole,
  shouldRedirectToRolesManage,
  needInviteCode,
  isAgreeProtocol,
  doupaidangShopType,
  closedInstitution,
  ECheckStatus,
  EDarenChild,
  isNeedShopAgreePro,
  EBindStar,
} from '@global/utils/user';
import {
  EShopType,
  EChildStatus,
  EAccountChildStatus,
  EChildShopType as EBuyinServer,
  EChildShopType,
  EInternalBusinessChild,
  EEcomPlatformChild,
  EBrandMerchantsChild,
  EInstitutionalServiceProvidersChild,
  EStatus,
  EAgreePro,
} from './common-user';
import { isObjectLike } from 'lodash';
export type TBuyinRole = EShopType | EChildStatus | EBuyinServer | EAccountChildStatus;
export type TOtherPlatformRole =
  | EChildShopType
  | EInternalBusinessChild
  | EEcomPlatformChild
  | EBrandMerchantsChild
  | EInstitutionalServiceProvidersChild;

export type IRole = TBuyinRole | TOtherPlatformRole;

// eslint-disable-next-line @typescript-eslint/explicit-function-return-type, @typescript-eslint/explicit-module-boundary-types
export function getUserInfo() {
  let userInfo: IUserInfo = {} as IUserInfo;
  try {
    userInfo = { ...JSON.parse(sessionStorage.userInfo) };
  } catch { }
  return userInfo;
}
/**
 * 饿汉单列模式
 * 提供最基本的 userInfo
 * 将 userInfo 收敛 无需关心 userInfo 和各种方法，直接调用所需要的角色判断
 */

let domainUser: DomainUser;
export class DomainUser {
  userInfo = {} as IUserInfo;
  // common role domain flat
  /**
   * 机构
   */
  isInstitution = false;
  /**
   * MCN
   */
  isMcn = false;
  /**
   * 团长
   */
  isCaptain = false;
  /**
   * 小店商家
   */
  isSmallShop = false;
  /**
   * daren
   */
  isDaren = false;
  /**
   * douke
   */
  isDouke = false;
  /**
   * tool service
   */
  isServiceProvider = false;

  // especial role
  /**
   * ecom platform
   */
  isEcomPlatform = false;
  /**
   * pop shop
   */
  isPopMerchants = false;
  /**
   * is shop type
   */
  isMergeShopList = false;
  // especial role flat
  role: IRole = EChildStatus.VISITOR;
  constructor(userInfo?: IUserInfo) {
    const _userInfo = (isObjectLike(userInfo) ? userInfo : getUserInfo()) as IUserInfo;
    this.userInfo = _userInfo;
    const { account_child_type, shop_type } = _userInfo;
    const shopType = shop_type;
    this.isCaptain = account_child_type === EChildShopType.ZHAO_SHANG_TUAN_ZHANG;
    this.isMcn = account_child_type === EChildShopType.MCN;
    this.isInstitution = shopType === EShopType.INSTITUTIONAL_SERVICE_PROVIDERS;
    this.isSmallShop = shopType === EShopType.INTERNAL_BUSINESS;
    this.isDaren = shopType === EShopType.DAREN;
    this.isDouke = shopType === EShopType.DOUKE;
    this.isServiceProvider = shopType === EShopType.INSTITUTIONAL_TOOLS_SERVICE_PROVIDERS;
    // especial role
    this.isEcomPlatform = shopType === EShopType.ECOM_PLATFORM;
    this.isPopMerchants = shopType === EShopType.BRAND_MERCHANTS;
  }
  static getInstance(userInfo?: IUserInfo): DomainUser {
    const hasUserInfo = isObjectLike(userInfo);
    if (domainUser) {
      return domainUser;
    }
    if (hasUserInfo) {
      const _userInfo =  hasUserInfo ? userInfo : getUserInfo();
      domainUser = new DomainUser(_userInfo);
      return domainUser;
    }
    return domainUser;
  }
  // login in douke
  normalDouke = () => {
    const { account_status, account_child_status, agree_pro } = this.userInfo;
    return (
      this.isDouke &&
      account_status === EStatus.OPEN &&
      account_child_status === EAccountChildStatus.NORMAL &&
      agree_pro === EAgreePro.AGREE
    );
  };

  // is normal shop
  normalUser = () => {
    const { userInfo } = this;
    if (needSelectRole(userInfo) || shouldRedirectToRolesManage(userInfo)) {
      return true;
    }

    return (
      !needInviteCode(userInfo) &&
      (isAgreeProtocol(userInfo) || this.isDaren || this.isSmallShop) &&
      !doupaidangShopType(userInfo) &&
      (closedInstitution(userInfo) || userInfo.status === EStatus.OPEN)
    );
  };

  // flash daren
  darenFlash = () => {
    const { userInfo } = this;
    const shopTypeChild = userInfo.shop_type_child;
    return this.isDaren && shopTypeChild === EDarenChild.DAREN_FLASH;
  };

  normalServiceProviders = () => {
    const { userInfo } = this;
    const shopType = userInfo.shop_type;
    const accountChildType = userInfo.account_child_type || userInfo.shop_type_child;
    return (
      shopType === EShopType.INSTITUTIONAL_SERVICE_PROVIDERS &&
      [EChildShopType.MCN, EChildShopType.ZHAO_SHANG_TUAN_ZHANG, EChildShopType.DOU_KE].includes(accountChildType)
    );
  };
  // visitor
  isVisitor = (): boolean => {
    const { userInfo } = this;
    return userInfo.check_status === ECheckStatus.PENDING && userInfo.status === EStatus.OPEN;
  };
  hasAllianceAuthSmallShop = (userInfo: IUserInfo): boolean =>
    this.isSmallShop && !this.isVisitor() && !isNeedShopAgreePro(userInfo);

  isBindStar = (): boolean => this.isMergeShopList && this.userInfo.has_bind_star === EBindStar.OPEN;

  hasMarketing = (): boolean => this.userInfo.marketing_create_coupon_auth === 1;

  getUserTypeName = (): string => {
    switch (true) {
      case this.isDaren:
        return 'daren';
      case this.isSmallShop:
        return 'shop';
      case this.normalServiceProviders():
        return 'institution';
      default:
        return '';
    }
  };
  getUserTypeID = (): string => {
    const { isDaren, isSmallShop, userInfo } = this;
    if (isDaren) {
      return userInfo.user_id;
    }
    if (isSmallShop) {
      return userInfo.shop_id;
    }
    if (this.normalServiceProviders()) {
      return userInfo.origin_uid;
    }
    return '';
  };
}

export function initStorageUserInfo(userInfo?: IUserInfo) {
  const domainUser = DomainUser.getInstance(userInfo);
  window.domainUser = domainUser;
  return {
    userInfo,
    domainUser,
  };
}

export {
  domainUser
}