import { IUserInfo } from "../role/type";
export interface IMetaMenu {
  name: string;
  url: string;
  entry?: string;
  isExternalSubApp?: boolean; // 是否是外部独立仓库的子应用
  componentPath?: string; // 指定的路由组件路径
  keyPath?: Array<string>;
  notRenderInMenu?: boolean | ((userInfo: IUserInfo) => boolean);
}

interface IMenuBase {
  expand?: boolean; // 需要下发
  keyPath?: string[];
  icon?: string; // 需要下发
  iconSvg?: string; // 需要下发
  name: string; // 需要下发
  url?: string; // 需要下发
  children?: IMenuProps[]; // 需要下发
  menu_id?: number;
}
export interface IMenuProps extends IMenuBase {
  // 菜单是否默认展开

  entry?: string;
  // 跳转链接获取方法
  href?: (userInfo: IUserInfo) => string;
  asyncHref?: (userInfo: IUserInfo) => Promise<string | undefined>;
  // 标记是否为跳转链接，需要下发
  isLink?: boolean;
  // 是否不显示在菜单栏，需要下发
  notRenderInMenu?: boolean | ((userInfo: IUserInfo) => boolean);
  active?: (userInfo: IUserInfo) => boolean;
  newmark?: boolean;
  hotTag?: boolean; // 是否需要展示菜单右侧的hot标签
  isExternalSubApp?: boolean; // 是否是外部独立仓库的子应用
  componentPath?: string; // 指定的路由组件路径
  guidesKey?: string; // 新手引导对应的key值（只有顶部导航有）
  headerMenuTag?: string; // 顶导气泡，最多只能有一个顶导气泡
  storageName?: string; // 控制气泡显示隐藏的本地存储字段名
  isMenuTagExpired?: boolean; // 气泡是否到期
}
export const getPath = (page: string) => `/dashboard${page}`;

export const findMenu = (rawMenu: IMenuProps[], storeMap: IMetaMenu[] = []) => {
  rawMenu.forEach((menu: IMenuProps) => {
    if (menu.url && !menu.isLink) {
      storeMap.push({
        name: menu.name,
        url: getPath(menu.url),
        isExternalSubApp: menu?.isExternalSubApp,
        componentPath: menu?.componentPath,
        keyPath: menu?.keyPath,
        notRenderInMenu: menu?.notRenderInMenu,
      });
    }
    if (menu.children) {
      findMenu(menu.children, storeMap);
    }
  });
  return storeMap;
};