import { MessageType } from 'antd/es/message/interface';

const defaultValue = 0;

export enum EStatus {
  DEFAULT = defaultValue,
  // 可使用功能
  OPEN = 1,
  // 无法进入百应，运营关闭
  OP_CLOSE = 2,
  // 游客身份，可使用功能、清退过
  TOURIST = 99,
}

export enum ECheckStatus {
  PENDING = 1, // 机构入驻中
  BANNED = 2, // 机构保证金不足清退中
  CHILD_ROLE_PENDING = 99, // 机构子角色入驻中
}

// 使用child_status区分商家可以使用的菜单权限
export enum EChildStatus {
  FORMAL = 1, // 正式版
  TRIAL = 2, // 试用版
  VISITOR = 3, // 游客身份
}

export enum EAgreePro {
  DEFAULT = defaultValue,
  NOT_AGREE = 1,
  AGREE = 2,
  AGREE_INSTITUTION = 3,
}

export enum EAccountChildStatus {
  NORMAL = 1,
  UNDER_CONTROL = 2,
  GUARANTEE_PENDING = 3,
}

export enum EShopType {
  DEFAULT = defaultValue,
  /**
   * @description 内部服务商：小店商家
   */
  INTERNAL_BUSINESS = 11,
  /**
   * @description 电商平台
   */
  ECOM_PLATFORM = 21,
  /**
   * @description 品牌商家：第三方商家
   */
  BRAND_MERCHANTS = 22,
  /**
   * @description 机构服务商
   */
  INSTITUTIONAL_SERVICE_PROVIDERS = 23,
  /**
   * @description 达人
   */
  DAREN = 24,
  /**
   * @description 机构-工具服务商
   */
  INSTITUTIONAL_TOOLS_SERVICE_PROVIDERS = 26,
  DOUKE = 27,
}

export enum EChildShopType {
  DEFAULT = defaultValue,
  MCN = 1, // 电商MCN
  ZHAO_SHANG_TUAN_ZHANG = 2, // 招商团长
  DOU_KE = 3, // 抖客
}

export enum EInternalBusinessChild {
  COMMON_MERCHANTS = 1,
  FRESH_BUYIN = 2,
}

export enum EEcomPlatformChild {
  ECOM_PLATFORM = 1,
}

export enum EBrandMerchantsChild {
  BRAND_MERCHANTS = 1,
}

export enum EInstitutionalServiceProvidersChild {
  COMMON_INSITUTIONAL_PROVIDERS = 1,
  DOUPAIDANG_BRAND_PROVIDERS = 2,
  DOUPAIDANG_TRAIN_PROVIDERS = 3,
  DOUPAIDANG_CONTENT_PROVIDERS = 4,
}

// 工具服务商子角色
export enum InstitutionToolsServiceProvidesChild {
  DEFAULT = 1,
}

export enum EDarenChild {
  DAREN = 1,
  DAREN_FLASH = 2,
  // 云闪购用户
  Cloud,
  // 玉石用户
  Jade,
}

// 权限开关
export enum EAuthSwitch {
  OPEN = 1,
  CLOSED = 0,
}

// 达人广场菜单权限开关
export enum EPlazaStatus {
  OPEN = 0,
  CLOSE = 1,
}

// 绑定星图开关
export enum EBindStar {
  OPEN = 1,
  CLOSE = 0,
}

export interface IUserAuth {
  authority_shop: EAuthSwitch; // 电商权限
  authority_item: EAuthSwitch;
  authority_live: EAuthSwitch; // 直播权限
}

/* eslint-disable @typescript-eslint/indent */
export interface IShopType {
  shop_id: string;
  shop_name: string;
  status: EStatus;
  agree_protocol: EAgreePro;
  shop_type: EShopType;
  user_name: string;
  shop_type_child:
  | typeof defaultValue
  | EInternalBusinessChild
  | EEcomPlatformChild
  | EBrandMerchantsChild
  | EInstitutionalServiceProvidersChild
  | EDarenChild;
}
/* eslint-enable @typescript-eslint/indent */

// 显示，隐藏
enum IS_MENU_SHOW {
  HIDE = 0,
  SHOW = 1,
}

// 选品广场菜单的展示和隐藏（ab实验临时用）
enum IS_MERCH_PICKING_HALL_SHOW {
  HIDE = 0,
  SHOW = 1,
}

export enum UserStatusType {
  IsOneShop = 1, // 是否是人店一体
  KolDoudianIdentity = 2, // 抖店侧配置，主推带货 or 主推店铺
}


export type IUserInfo = UserStatus & ISubUserInfo;

export enum OneShopType {
  'mainShop' = 1, // 主推店铺
  'mainProduct' = 2, // 主推带货
}

export interface UserStatus {
  inst_enter_gray?: number; // 1 是命中，否则不明中灰度
  status?: number; // 是否支持开票，0为关闭，1为开启
  new_bind?: number; // 上下文 https://bytedance.feishu.cn/docx/RfQGd8RfSogidJxmWKocYyfOnKh
  // is_oneshop?: boolean; // 是否人店一体
  // kol_doudian_identity_type?: OneShopType;
}

// 商家账号类型，0 未知，1 主账号，2 子账号
export enum ShopLoginIdentityEnum {
  UNKNOWN,
  MAIN_ACCOUNT,
  SUB_ACCOUNT,
}

// 连接策略类型
export enum ConnectionType {
  Poll = 1, // 短轮询
  Persistent = 2, // 长连接
  Mix = 3, // 混合
}

export interface ConnectionConfig {
  type: ConnectionType; // 链接策略类型
  persist_types: MessageType[]; // ConnectionType为Mix时下发使用长连接的场景，仅ConnectionType为Mix时有效，用于部分降级
  tt_wid?: string; // webid
  access_key?: string; // 长连接access_key
}
export interface ISubUserInfo {
  account_avatar?: string; // 账号头像
  check_live_base?: boolean;
  buyin_account_id?: string; // 账户id
  user_id: string;
  shop_id: string;
  official_shop_id?: string;
  origin_uid: string;
  shop_name: string;
  user_name: string;
  status: EStatus;
  check_status: ECheckStatus;
  child_status?: EChildStatus;
  shops: IShopType[];
  /**
   * @description 是否同意协议
   */
  agree_pro: EAgreePro;
  shop_type: EShopType;
  shop_type_child:
  | typeof defaultValue
  | EInternalBusinessChild
  | EEcomPlatformChild
  | EBrandMerchantsChild
  | EInstitutionalServiceProvidersChild
  | EDarenChild;
  daren_plaza_status?: number;
  daren_plaza_popup?: boolean;
  plaza_status?: EPlazaStatus;
  // 是否绑定星图
  has_bind_star?: EBindStar;
  center_menu_show?: 0 | 1;
  qianchuan?: 0 | 1;
  compass_second_level_menu_show?: IS_MENU_SHOW;
  compass_first_level_menu_show?: IS_MENU_SHOW;
  marketing_create_coupon_auth?: 1 | 2;
  anchor_coupon_menu_show?: 0 | 1;
  // 达人是否填写联系方式
  contact_not_set?: 0 | 1;
  // 手机号
  user_mobile?: string;
  doudian_shop_id?: number | string;
  selection_plaza?: IS_MERCH_PICKING_HALL_SHOW;
  // 是否是KV用户
  isKeyAccount?: number;
  // 是否显示发红包入口
  redpacket_entrance?: number;
  // 求讲解按钮是否显示
  ask_explain_info_show?: boolean;
  is_real_one_shop?: boolean; // 是否人店一体
  kol_doudian_identity_type?: OneShopType;
  author_level: number; // -1 ~ 7 -1未知 7星推官
  is_possess_login?: number; // 是否运营附身，1即为附身
  commission_ticket_white_list?: boolean; // 发票配置 true  在白名单  false  不在
  trade_online_white_list?: boolean; // 线上交易化白名单 true  在白名单  false  不在
  shop_login_identity?: ShopLoginIdentityEnum; // 商家账号类型
  connection_config?: ConnectionConfig;
  create_time?: string;
  is_flagship?: boolean; // 是否是旗舰店
  account_status?: number; // 账户状态
  account_child_type?: number | null; // 账户子角色
  account_child_status?: EAccountChildStatus;
  show_shop_assistant?: boolean; // 控制商家助手灰度
  shop_need_agree_pro?: boolean; // 是否需要签署协议
}

// 小店 / 第三方商家的联系方式
export interface IUserContact {
  mask_phone_num: string;
  contact_name: string;
  has_phone_num: boolean;
  has_lark_num: boolean;
  mask_lark_num: string;
  lark_nick_name: string;
}
/* eslint-enable @typescript-eslint/indent */

export const defaultUserInfo: IUserInfo = {
  user_id: '',
  buyin_account_id: '',
  shop_id: '',
  origin_uid: '',
  shop_name: '',
  user_name: '',
  status: EStatus.DEFAULT,
  check_status: ECheckStatus.BANNED,
  shops: [],
  agree_pro: EAgreePro.DEFAULT,
  shop_type: defaultValue,
  shop_type_child: defaultValue,
  has_bind_star: defaultValue,
  is_real_one_shop: false,
  kol_doudian_identity_type: OneShopType.mainProduct,
  author_level: -1,
};
