  // 上报PV/UV
  sendPageView(): void {
    this.sendEvent?.('predefine_pageview', {
      title: document.title,
    });
  }

  // 监听路由变化
  watchRouteChange() {
    const handler = () => {
      // this.setConfig(); // 主要为了设置通用参数
    };
    window.addEventListener('popstate', handler);
    return () => {
      window.removeEventListener('popstate', handler);
    };
  }

  // 当前页面的一些参数
  private getLocationConfig = () => {
    return {
      url: window.location.href,
      url_path: window.location.pathname,
      enter_from: getQuery().enter_from || '', // 页面来源
    };
  };
