
import React, { useState } from 'react';
import Icon, {
  LinkOutlined,
  SettingOutlined,
  MenuFoldOutlined,
  MenuUnfoldOutlined,
} from '@ant-design/icons';

import { Button, Divider, Menu, Switch } from 'antd';
import type { MenuProps, MenuTheme } from 'antd/es/menu';
import s from './menu.m.scss';
import { useRecoilValue } from 'recoil';
import { storeMenu } from 'src/global/model/menu';
import { IMenuProps } from 'src/global/model/menu/service';
import { ItemType, MenuItemType } from 'antd/es/menu/hooks/useItems';


import { useHistory } from "react-router-dom";
import { checkURL } from 'src/global/utils/regexp';

type MenuItem = Required<MenuProps>['items'][number]


function getItem(
  label: React.ReactNode,
  key?: React.Key | null,
  icon?: React.ReactNode,
  children?: MenuItem[],
): MenuItem {
  return {
    key,
    icon,
    children,
    label,
  } as MenuItem;
}

/**
 * 递归获取菜单
 * 注意不要有循环递归
 */
export function getMenuItemsByRoute(navRoute: IMenuProps[]): ItemType[] {
  return navRoute?.map((menu: IMenuProps) => {
    // if (menu.iconSvg) {
    //   registerIcon(menu.iconSvg, menu.icon);
    // }
    const ReactEle = <LinkOutlined />; // menu.iconSvg ? <Icon component={<ReactSvg svgString={menu.iconSvg} /> } /> : <></>;
    const { url, storageName } = menu ?? {};
    const urlKey = url || storageName;
    const urlUnique = urlKey || urlKey + Math.random().toString();
    // console.log('url_suffix_cnt',urlKey, urlUnique)
    if (menu?.children?.length) {
      const curMenu = {
        ...menu,
        children: [],
      }
      const mergeCurMenuChildren = urlKey ? [curMenu, ...menu?.children] : [...menu?.children];
      return  getItem(menu.name, urlUnique, <SettingOutlined />, getMenuItemsByRoute(mergeCurMenuChildren)) ;
    } else {
      return  getItem(menu.name, urlUnique, ReactEle)  ;
    }
  })
}

interface IInnerMenuProps extends MenuProps {
  collapsed?: boolean;
  navRoute: IMenuProps[];
}

const InnerMenu: React.FC<IInnerMenuProps> = (props: IInnerMenuProps) => {
  const { onSelect, navRoute = [] } = props;
  const [mode, setMode] = useState<'vertical' | 'inline'>('inline');
  const [theme, setTheme] = useState<MenuTheme>('light');
  const [collapsed, setCollapsed] = useState(false);


  const changeMode = (value: boolean) => {
    setMode(value ? 'vertical' : 'inline');
  };

  const changeTheme = (value: boolean) => {
    setTheme(value ? 'dark' : 'light');
  };
  const toggleCollapsed = () => {
    setCollapsed(!collapsed);
  };
  return (
    <>
      <section className={s.menu_settings}>
        <Button type="primary" onClick={toggleCollapsed} >
          {collapsed ? <MenuUnfoldOutlined /> : <MenuFoldOutlined />}
        </Button>
        <Switch onChange={changeMode} />
        <Divider type="vertical" />
        <Switch onChange={changeTheme} />
      </section>
      <Menu
        // route={getMenuItemsByRoute(navRoute)}
        items={getMenuItemsByRoute(navRoute)}
        style={{ width: 256 }}
        defaultSelectedKeys={['1']}
        defaultOpenKeys={['sub1']}
        mode={mode}
        theme={theme}
        inlineCollapsed={collapsed}
        onSelect={onSelect}
      />
    </>
  );
};

export function NavSideMenu() {

  const onSelect = (params: any) => {
    const { item, key, keyPath, selectedKeys, domEvent } = params || {}
    // console.log('onSelect', params, item, key, keyPath, selectedKeys, domEvent)
    // console.log('menu:', params);
    if (checkURL(params?.key)) {
      window.open(params?.key, '_blank')
    } else {
      window.history.pushState({}, params?.key ?? 'params?.key', params?.key)
    }
  }

  const storeMenuValue = useRecoilValue(storeMenu);
  const { navRoute } = storeMenuValue;
  return (
    <div className={s.wrap_menu}>
      <InnerMenu onSelect={onSelect} navRoute={navRoute} />
    </div>
  )
}