import React from 'react';
import { FallbackProps } from 'react-error-boundary';
import s from './error-fallback.m.scss';

export const ErrorFallback = ({ error, resetErrorBoundary }: FallbackProps) => {
  return (
    <div className={s.headerWrapper} data-msg={error?.message}>
      <div onClick={resetErrorBoundary}>加载失败，请稍后重试</div>
    </div>
  );
};

// 错误提示
export const SubAppErrorFallback = ({ error, resetErrorBoundary }: FallbackProps) => {
  return (
    <div data-msg={error?.message} className={s.subappWrapper}>
      <img src={'offlineBackgroundImgRes'} />
      <div className={s.tip} onClick={resetErrorBoundary}>
        加载失败，请稍后重试
      </div>
    </div>
  );
};
