// import { INSTITUTION_MODULE_FEDERATION_REMOTE_URL } from '@constants/institution-remote-route';
import { Spin } from 'antd';
import React from 'react';
import useFederatedComponent from './hooks';

export enum RemoteType {
  INSTITUTIONS = 'institutions',
}

interface FederatedComponentProps {
  [key: string]: unknown;
  remoteEntry?: string;
  remoteType?: string;
  scope: string;
  module: string;
}

// const remoteTypeEntry = {
//   [RemoteType.INSTITUTIONS]: INSTITUTION_MODULE_FEDERATION_REMOTE_URL,
// };

const FederatedComponent: React.FC<FederatedComponentProps> = ({
  remoteEntry,
  remoteType,
  scope,
  module,
  ...props
}) => {
  const _remoteEntry = remoteEntry ?? ''; // ?? remoteTypeEntry[remoteType ?? ''];
  const { errorLoading, Component } = useFederatedComponent(_remoteEntry, scope, `./${module}`);
  return (
    <React.Suspense fallback={<Spin size="large" tip="正在加载..." />}>
      {errorLoading ? null : Component && <Component {...props} />}
    </React.Suspense>
  );
};

export default FederatedComponent;
