export function loadComponent(scope: string, module: string) {
  return async () => {
    const factory = await window[scope].get(module);
    const Module = factory();
    return Module;
  };
}

export const useFederatedComponent = (remoteUrl: string, scope: string, module: string) => {
  const key = `${remoteUrl}-${scope}-${module}`;

  const [componentLoading, setComponentLoading] = useState(false);
  const [Component, setComponent] = useState<React.ComponentType<any> | null>(null);

  const { ready, errorLoading } = useDynamicScript(remoteUrl);

  useEffect(() => {
    setComponentLoading(false);
  }, [key]);

  useEffect(() => {
    if (ready) {
      const Comp = lazy(loadComponent(scope, module));
      setComponent(Comp);
    }
  }, [module, ready, scope]);

  return { errorLoading, componentLoading, Component };
};

export default useFederatedComponent;
