export enum EInternalBusinessChild {
  one,
  tow,
}
export enum EShopType {
  DEFAULT = 0,
  /**
   * @description 内部服务商：小店商家
   */
  INTERNAL_BUSINESS = 11,
  /**
   * @description 电商平台
   */
  ECOM_PLATFORM = 21,
  /**
   * @description 品牌商家：第三方商家
   */
  BRAND_MERCHANTS = 22,
  /**
   * @description 机构服务商
   */
  INSTITUTIONAL_SERVICE_PROVIDERS = 23,
  /**
   * @description 达人
   */
  DAREN = 24,
  /**
   * @description 机构-工具服务商
   */
  INSTITUTIONAL_TOOLS_SERVICE_PROVIDERS = 26,
}
export type TUserInfo = EShopType;
export enum EStatus {
  one,
  tow,
}
export interface ISubUserInfo {
  account_avatar?: string; // 账号头像
  check_live_base?: boolean;
  buyin_account_id?: string; // 账户id
  user_id: string;
  shop_id: string;
  official_shop_id?: string;
  origin_uid: string;
  shop_name: string;
  user_name: string;
  status: EStatus;
  check_status: EStatus;
  child_status?: EStatus;
  user_app: EStatus;
  shops: EShopType[];
  dr_auth?: Record<string, EStatus>;
  /**
   * @description 是否同意协议
   */
  agree_pro: EStatus;
  shop_type: EShopType;
  shop_type_child: EInternalBusinessChild
  daren_plaza_status?: number;
  daren_plaza_popup?: boolean;
  // 是否展示中控菜单, 临时使用, 1=展示
  center_menu_show?: 0 | 1;
  // 是否千川投放, 临时使用, 1=展示
  qianchuan?: 0 | 1;
  // 是否展示 数据参谋-更多数据 菜单
  // 个人店铺（个体工商户除外）关闭达人营销授权入口（主播券）1：有权限，2：无权限
  marketing_create_coupon_auth?: 1 | 2;
  anchor_coupon_menu_show?: 0 | 1;
  // 达人是否填写联系方式
  contact_not_set?: 0 | 1;
  // 手机号
  user_mobile?: string;
  // 主子账号
  // 是否人店一体: 字段存在且大于0，则达人有人店一体的店铺
  doudian_shop_id?: number | string;
  // 是否显示选品广场菜单：如果对应value为1 显示 反之不显示。精选联盟pc版ab实验用
  // 是否是KV用户
  isKeyAccount?: number;
  // 是否显示发红包入口
  redpacket_entrance?: number;
  // 求讲解按钮是否显示
  ask_explain_info_show?: boolean;
  is_real_one_shop?: boolean; // 是否人店一体
  author_level: number; // -1 ~ 7 -1未知 7星推官
  is_possess_login?: number; // 是否运营附身，1即为附身
  commission_ticket_white_list?: boolean; // 发票配置 true  在白名单  false  不在
  trade_online_white_list?: boolean; // 线上交易化白名单 true  在白名单  false  不在
  create_time?: string;
  is_flagship?: boolean; // 是否是旗舰店
  account_status?: number; // 账户状态
  account_child_type?: number | null; // 账户子角色
  show_shop_assistant?: boolean; // 控制商家助手灰度
  shop_need_agree_pro?: boolean; // 是否需要签署协议
  is_banned?: boolean; // 商家是否被清退过字段
}

interface UserStatus {
  has_bind_star?: number;
}

export interface IUserInfo extends ISubUserInfo, UserStatus {
  key: string;
}
