import { atom } from "recoil";
import { modelStateMap } from "../common";
import { IMenuProps, findMenu } from "./service";
const rawRouteAll: IMenuProps[] = require('../mock/routeAll.json');
const navRoute: IMenuProps[] = require('../mock/nav_route.json');

const microRoutes = findMenu(rawRouteAll as unknown as IMenuProps[]);
const subAppRoutes = microRoutes.filter(item => !item?.isExternalSubApp);
const defMenu = {
  subAppRoutes,
  rawRouteAll,
  navRoute
}
export const storeMenu = atom({
  key: modelStateMap.menu, // unique ID (with respect to other atoms/selectors)
  default: defMenu,
});